.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 24
}

html {
  scroll-behavior: smooth;
}

 body{
  background-color: #fff;
 }


/* home */

.item-content:hover .item-icon{
  transform: rotate(-10deg);
}


.first-section{
  background-image: url(assets/shapes/shapes-19.png);
  background-position: top left;
  background-repeat: no-repeat;
  background-attachment: scroll;
}

.right-cut{
  clip-path: polygon(
              /* Top-left */
              0% 0,
  
              0 0,
              /* top-right */
              100% 0,
              100% calc(100% - 8em),
  
              /* bottom-right 1 */
              calc(100% - 8em) 100%,
              /* bottom-right 2 */
              0 100%);
}

@media screen and (max-width:991px) {
  .right-cut{
    clip-path: polygon(
                /* Top-left */
                0% 0,
    
                0 0,
                /* top-right */
                100% 0,
                100% calc(100% - 5em),
    
                /* bottom-right 1 */
                calc(100% - 5em) 100%,
                /* bottom-right 2 */
                0 100%);
  }
}


[dir=ltr] .right-cut{
  clip-path: polygon(20% 0%, 80% 0%, 100% 0, 100% 80%, 100% 100%, 10% 100%, 0% 70%, 0 0);
}

@media screen and (max-width:991px) {
  [dir=ltr] .right-cut{
    clip-path: polygon(20% 0%, 80% 0%, 100% 0, 100% 80%, 100% 100%, 20% 100%, 0% 80%, 0 0);
  }
}


.hero-bg{
  background-image: url(assets/backgrounds/hero-background-01.webp);
  background-size: cover;
  background-position: center;
}

.gray-background{
  background-image: url(assets/backgrounds/gray-background-02.svg);
}

.has-gradient{
  background-image: -webkit-linear-gradient(89.64deg, #0A588A 0.32%, #0A588A 32.07%, #6DCCDE 60.55%, #0A588A 99.28%);
  background-image: -moz-linear-gradient(89.64deg, #0A588A 0.32%, #0A588A 32.07%, #6DCCDE 60.55%, #0A588A 99.28%);
  background-image: -ms-linear-gradient(89.64deg, #6DCCDE 0.32%, #0A588A 32.07%, #6DCCDE 60.55%, #0A588A 99.28%);
  background-image: -o-linear-gradient(89.64deg, #0A588A 0.32%, #0A588A 32.07%, #6DCCDE 60.55%, #0A588A 99.28%);
  background-image: linear-gradient(89.64deg, #0A588A 0.32%, #0A588A 32.07%, #6DCCDE 60.55%, #0A588A 99.28%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  -moz-animation: sectionPreTitle 3s ease-in-out 1s forwards infinite alternate;
  -webkit-animation: sectionPreTitle 3s ease-in-out 1s forwards infinite alternate;
  animation: sectionPreTitle 3s ease-in-out 1s forwards infinite alternate;
  background-size: 150% 150%;
}

@-webkit-keyframes sectionPreTitle {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}
@-moz-keyframes sectionPreTitle {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}
@-ms-keyframes sectionPreTitle {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}
@keyframes sectionPreTitle {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 100%;
  }
}

