  .bounce{
    animation: bounce2 5s infinite;
  }


  @keyframes bounce2 {
    0%, 100% {
      transform: translateX(-25%);
      animation-timing-function: cubic-bezier(10, 1, 0.2, 100);
    }
    50% {
      transform: translateX(0);
      animation-timing-function: cubic-bezier(10, 1, 0.2, 40);
    }
  }